.modal{
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.7);
    z-index: 99;
}

.modal .container{
    position: fixed;
    max-width: 600px;
    top: 15%;
    left: 0;
    right: 0;
    margin: 0 auto;
    padding: 4em 2em;
    background-color: #fff;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.8);
}

.close{
    background-color: #f65835;
    border: 0;
    border-radius: 5px;
    position: absolute;
    top: 15px;
    left: 15px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 30px;
    width: 100px;
    color: #fff;
    font-size: 0.9rem;
    transition: all 0.3s;
}

.close:hover{
    opacity: 0.8;
}

.close svg{
    margin-right: 5px;
}

.modal h2{
    margin-bottom: 1.4rem;
}

.modal span{
    font-weight: bold;
    font-size: 1rem;
}

.modal span i{
    font-weight: 400;
    margin-right: 1rem;
    padding: 2px 8px;
}

.row{
    margin-bottom: 0.8rem;
}

.modal p{
    padding-top: 0.5rem;
    white-space: pre-wrap;
    line-height: 150%;
}

.status-badge{
    margin-left: 10px;
    border-radius: 3px;
}